/* ==============================
  中央寄せ
============================== */
body {
  font-family: "Noto Serif JP", sans-serif;
  font-size: 16px;
  letter-spacing: 0.9px;
}

.sans {
  font-family: "Noto Sans JP", sans-serif;
}

/* =================================
  background
================================== */
.bg-orange {
  background: var(--orange);
}
.bg-orange.pats {
  position: relative;
}
.bg-orange.pats:before {
  position: absolute;
  top: -12px;
  left: 0;
  content: "";
  display: block;
  width: 100%;
  height: 10px;
  background: var(--orange);
  border-bottom: 2px solid #000;
}
.bg-orange.pats:after {
  bottom: -12px;
  left: 0;
  content: "";
  display: block;
  width: 100%;
  height: 10px;
  background: var(--orange);
  border-top: 2px solid #000;
}

/* =================================
  container
================================== */
.container {
  width: 86%;
  max-width: 840px;
  margin-left: auto;
  margin-right: auto;
  padding-top: 3.5rem;
  padding-bottom: 6rem;
}
.container.--sm {
  width: 80%;
  margin-left: auto;
  margin-right: auto;
}
.container.--lg {
  width: 93%;
  margin-left: auto;
  margin-right: auto;
  padding-bottom: 2rem;
}

